@import "helpers/variables";

.saa-sidepanel-body {
    margin: 0 !important;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: auto;

    &::-webkit-scrollbar {
        width: 0;
    }
  
    &.scrolling {
        &::-webkit-scrollbar {
            width: 10px;
        }
        &::-webkit-scrollbar-thumb {
            background-color: #888;
            border-radius: 10px;
        }
        &::-webkit-scrollbar-thumb:hover {
            background-color: #555;
        }
    }

    .position-fixed {
        position: fixed;
    }

    .cursor-pointer {
        cursor: pointer;
    }

    .saa-color-green {
        color: #007600;
    }

    .saa-color-red {
        color: rgb(177, 39, 4);
    }

    .d-contents {
        display: contents;
    }

    .justify-space-between {
        justify-content: space-between;
    }

    .h-32 {
        height: 32px;
    }

    .h-44 {
        height: 44px;
    }

    .h-59 {
        height: 59px;
    }

    .h-70 {
        height: 70px;
    }

    .h-inherit {
        height: inherit !important;
    }

    .mt-1 {
        margin-top: 2px;
    }

    .mt-20 {
        margin-top: 20px;
    }

    .mt-22 {
        margin-top: 22px;
    }

    .mr-10 {
        margin-right: 10px;
    }

    .mr-12 {
        margin-right: 12px;
    }

    .mb-14 {
        margin-bottom: 14px;
    }

    .r-22 {
        right: 22px !important;
    }

    .fs-14 {
        font-size: 14px !important;
    }

    .fs-13 {
        font-size: 13px !important;
    }

    .bold-text {
        font-weight: 500;
    }

    .marker-hide {
        list-style-type: none;
    }

    .text-right {
        text-align: right;
    }

    .max-width-250 {
        max-width: 250px;
    }

    .max-width-content {
        max-width: 526px;
    }

    .saa-highlight-green {
        margin: 0 0.2rem;
        padding: 0.2rem;
        background: rgba(21, 185, 29, 0.1);
        border-radius: 5px;
    }

    .saa-highlight-red {
        margin: 0 0.2rem;
        padding: 0.2rem;
        background: rgba(185, 73, 21, 0.1);
        border-radius: 5px;
    }

    .saa-tooltip-container {
        position: relative;
        &.static {
            position: static;
        }
        .saa-ul-default {
            list-style: none;
            margin-bottom: 0;
        }
    }
    .a-icon {
        display: inline-block;
        vertical-align: top;
    }
    .a-icon.a-star-5 {
        background-position: -84px -220px
    }
    .a-icon.a-star-4 {
        background-position: -84px -240px
    }
    .a-icon.a-star-3 {
        background-position: -181px -284px
    }
    .a-icon.a-star-2 {
        background-position: -360px -296px
    }
    .a-icon.a-star-1 {
        background-position: -278px -292px
    }
    .a-icon.a-star-0 {
        background-position: -196px -304px
    }
    .a-icon.a-star-4-5 {
        background-position: -84px -260px
    }
    .a-icon.a-star-3-5 {
        background-position: -99px -280px
    }
    .a-icon.a-star-2-5 {
        background-position: -375px -316px
    }
    .a-icon.a-star-1-5 {
        background-position: -278px -312px
    }
    .a-icon.a-star-0-5 {
        background-position: -375px -336px
    }
    .a-icon-star, .a-icon-star-medium, .a-icon-star-mini, .a-icon-star-small {
        position: relative;
        vertical-align: text-top;
        background-image: url(https://m.media-amazon.com/images/S/sash/fe7v9cZH5dQOFva.png);
        background-size: 512px 512px;
        background-repeat: no-repeat;
    }
    .a-icon-star, .a-icon-star-small {
        height: 18px;
        width: 80px;
    }

    .saa-cog-tooltip-row {
        border-top: 1px solid $light-gray;
        width: 100%;
        justify-content: space-between;
        display: flex;
        flex: 1 1 0;
        .saa-cog-tooltip-col {
            display: flex;
            flex-direction: column;
            flex: 1 1 0;
            padding: 0;
            border-right: 1px solid $light-gray;
            &:last-child {
                border-width: 0;
                border-style: initial;
                border-color: initial;
                border-image: initial;
            }
            .saa-cog-tooltip-header {
                border-bottom: 1px solid $light-gray;
                background: rgba(248, 248, 248, 1);
            }
            .saa-cog-tooltip-item {
                width: 100%;
                text-align: center;
                white-space: nowrap;
                padding: 0.3rem 0.5rem;
                font-size: 12px;
                &.mt-0 {
                    margin-top: 0px !important;
                }
                .saa-input-vat {
                    height: 22px;
                    width: 40px;
                    border-radius: 5px;
                    font-size: 12px;
                    text-align: center;
                    border: 1px solid;
                }
                /* Chrome, Safari, Edge, Opera */
                input::-webkit-outer-spin-button,
                input::-webkit-inner-spin-button {
                    -webkit-appearance: none;
                    margin: 0;
                }
                /* Firefox */
                input[type=number] {
                    -moz-appearance: textfield;
                }
            }
            .saa-cog-tooltip-item:last-child {
               margin-top: 5px;
            }
        }
    }

    .saa-tooltip {
        position: absolute;
        background-color: #fff;
        padding: 10px;
        border-radius: 5px;
        z-index: 10;
        box-shadow: 0px 2px 9px 0px #0F11111A;
        font-size: 12px;
        font-weight: 400;
        line-height: 15px;
        letter-spacing: 0em;
        text-align: left;
        min-width: 250px;
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
        &.position-roi {
            margin-right: 104px;
            &.t-28 {
                margin-top: 28px;
            }
            &.t-52 {
                margin-top: 52px;
            }
        }
        &.position-profit {
            margin-left: 104px;
            &.t-28 {
                margin-top: 28px;
            }
            &.t-52 {
                margin-top: 52px;
            }
        }
        &.position-bep {
            margin-top: -2px;
            margin-left: -132px;
        }
        &.position-sales {
            margin-top: 112px;
            max-width: 250px;
            margin-left:104px;
        }
        &.position-input {
            min-width: fit-content;
            top: 8px;
        }
        &.position-copy-category {
            min-width: max-content;
            left: -85px;
        }
        &.position-category {
            max-width: 250px;
            margin-top: 80px;
        }
        &.position-bsr {
            margin-top: -2px;
            margin-left: -8px;
        }
        &.position-bsr-value {
            margin-left: -85px;
            margin-top: -2px;
        }
        &.position-bottom {
            min-width: max-content;
            position: fixed;
            left: 50px;
            &.b104 {
                bottom: 104px;
            }
            &.b64 {
                bottom: 64px;
            }
            &.b20 {
                bottom: 20px;
            }
        }
        &.position-details {
            min-width: max-content;
            position: fixed;
            left: 50px;
            top: 61px;
        }
        &.position-search {
            min-width: max-content;
            position: fixed;
            left: 50px;
            top: 20px;
        }
        &.position-img-icons {
            left: -5px;
            min-width: fit-content;
            text-wrap: nowrap;
        }
        &.left-50 {
            left: -50px;
        }
        &.left-110 {
            left: -110px;
        }
        &.left-115 {
            left: -115px;
        }
        &.left-150 {
            left: -150px;
        }
        &.left-190px {
            left: -190px;
        }
        &.left-220 {
            left: -220px;
        }
        &.position-1 {
            max-width: 175px;
            min-width: 175px;
            left: -95px;
        }
        &.position-2 {
            left: -5px;
            min-width: fit-content;
            text-wrap: nowrap;
        }
        &.position-title {
            left: -110px;
            max-width: 250px;
            &.brand {
                min-width: 190px;
            }
        }
        &.position-link {
            left: -70px;
            max-width: 250px;
            min-width: 100px;
        }
        &.position-asin {
            left: -66px;
            max-width: 120px;
            min-width: max-content;
        }
        &.position-copy-brand {
            left: -70px;
            max-width: 120px;
            min-width: max-content;
        }
        p {
            margin-bottom: 5px;
        }
        p:last-child {
            margin-bottom: 0 !important;
        }
        .saa-bsr-tooltip-row {
            justify-content: space-between;
            display: flex;
            flex: 1;
        }
        .saa-bsr-tooltip-col {
            display: flex;
            flex: 1;
            flex-direction: column;
            padding: 0;
            border-right: 1px solid #d3d3d3ff;
        }
        .saa-bsr-tooltip-col:last-child {
            border: 0;
        }
        .saa-bsr-tooltip-item {
            padding: 0.3rem 0.5rem;
            width: 100%;
            flex: 1 0 1.5rem;
            text-align: center;
            white-space: nowrap;
        }
        .saa-bsr-tooltip-header {
            border-bottom: 1px solid #d3d3d3ff;
        }
        table {
            width: 100%;
            th {
                text-align: left;
            }
            tr td:last-child {
                text-align: center;
            }
        }
    }
      
    .target-saa-tooltip:hover + .saa-tooltip,.saa-tooltip:hover {
        opacity: 1;
        visibility: visible;
        transition-delay: 0.3s;
        pointer-events: auto;
    }

    .saa-sidepanel-link {
        color: #007185;
        cursor: pointer;
        &:hover {
            color: rgb(236, 99, 55);
        }
        &.padding-start {
            padding-right: 6px;
            text-align: start;
        }
    }

    textarea:focus {
        border-color: #80bdff !important;
        outline: 0;
    }

    .saa-sidepanel-link-svg {
        cursor: pointer;
        &:hover {
            path:last-child {
                fill: #ec6337;
            }
            path:not(:last-child) {
                stroke: #ec6337;

            }
        }
    }

    .saa-btn {
        box-shadow: 0px 4px 6px -4px #3A35411A;
        width: 197px;
        border-radius: 5px;
        text-transform: uppercase;
        padding: 5px 0px 5px 0px;
        font-size: 13px;
        font-weight: 500;
        line-height: 22px;
        letter-spacing: 0px;
        border: none;
        background: #FFD814;
    }

    .saa-sidepanel-container {
        display: flex;
        height: inherit;

        .input-box {
            margin-top: 20px;
            border-radius: 6px;
            background: #F8F8F8;
            display: flex;
            align-items: center;
            padding-left: 0.5rem;
            overflow: hidden;
            width: 100%;
            input[type="text"][disabled] {
                color: #212529;
            }
            &.active {
                .arrow {
                    transform: rotateX(180deg);
                }
            }
            &.max-105 {
                max-width: 105px;
            }
            .dropdown {
                right: 0px;
                max-height: 300px;
                overflow-y: scroll;
                top: 56px;
                display: flex;
                flex-direction: column;
                position: absolute;
                background-color: #f1f1f1;
                width: 105px;
                box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
                z-index: 3;
                border-radius: 5px;
                font-size: 14px;
                a {
                    text-decoration: none;
                    padding-left: 15px;
                    padding-top: 4px;
                    padding-bottom: 4px;
                    color: #212529;
                    &:hover {
                        background: #c8c8c8;
                    }
                    &.select {
                        background: #c8c8c8;
                    }
                }
                
            }
        }
          
        .input-box .prefix {
            font-weight: 300;
            font-size: 14px;
            color: #000;
            opacity: 0.5;
            cursor: pointer;
            &:hover {
                opacity: .75;
            }
        }
          
        .input-box input {
            flex-grow: 1;
            font-size: 14px;
            border: none;
            outline: none;
            background: #F8F8F8;
            border: none;
            padding: 10px;
            width: 100%;
            &.search {
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: nowrap;
                padding: 0px 0px 0px 6px !important
            }
        }

        .input-box .close {
            padding-right: 0.5rem;
            font-size: 14px;
            cursor: pointer;
        }

        .input-box .arrow {
            padding: 0.5rem;
            font-size: 14px;
            cursor: pointer;
        }
        
        .input-box:focus-within {
            border-color: #777;
        }

        .saa-sidepanel-loading {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;

            &.short {
                padding: 20px 0px 8px 0px;
                max-width: 526px;
                min-width: 290px;
            }

            &.calc {
                height: 297px;
            }
        }

        .icon-spinner {
            animation: spin-animation 1.5s infinite;
            display: inline-block;
        }
        
        @keyframes spin-animation {
            0% {
                transform: rotate(0deg);
            }
            100% {
                transform: rotate(359deg);
            }
        }

        .saa-sidepanel-start {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            width: 100%;

            .title {
                padding: 0px 20px 0px 20px;
                font-size: 16px;
                font-weight: 500;
                line-height: 20px;
                text-align: center;
                margin-bottom: 20px;

                &.login {
                    margin-bottom: 6px !important;
                }

                &.max-360 {
                    max-width: 360px;
                }
            }

            .sub-title {
                font-size: 14px;
                font-weight: 400;
                line-height: 18px;
                letter-spacing: 0px;
                text-align: center;
                margin-bottom: 20px;
                color: #555555;
                &.subscribe {
                    font-size: 16px;
                    font-weight: 500;
                    line-height: 21.76px;
                    text-align: center;
                    max-width: 330px;
                    color: #0E081A;
                }
            }

            .saa-sidepanel-arrow {
                position: relative;
                left: 120px;
                top: -30px;
                z-index: 1;
            }

            .saa-sidepanel-qv-product {
                position: relative;
                top: -45px;
                left: 15px;

                .blur-2 {
                    filter: blur(2px);
                }

                .sidepanel-btn {
                    position: relative;
                    left: -40px;
                    top: -40px;
                }
            }
        }

        .component-fade-enter-active, .component-fade-leave-active {
            transition: opacity .3s ease;
        }
        .component-fade-enter, .component-fade-leave-to {
            opacity: 0;
        }

        .saa-sidepanel-navbar {
            position: fixed;
            overflow: hidden !important;
            inset: 0;
            z-index: 9;
            width: 46px;
            display: flex;
            flex-direction: column;
            align-items: center;
            border-right: 1px solid #D3D3D3;
            padding: 20px 0px;
            padding-right: 0px !important;
            justify-content: space-between;

            .saa-sidepanel-top-nav, .saa-sidepanel-bottom-nav {
                .nav-icon {
                    display: flex;
                    background: #F8F8F8;
                    padding: 5px;
                    border-radius: 5px;
                    svg {
                        color: #007185;
                    }
                    &:hover {
                        cursor: pointer;
                        background: #007185 !important;
                        svg {
                            color: white !important;
                        }
                    }
                    &.active {
                        background: #007185 !important;
                        svg {
                            color: white !important;
                        }
                    }
                }
                .nav-icon-link {
                    &:hover {
                        cursor: pointer;
                        background: #007185 !important;
                        svg {
                            color: white !important;
                        }
                    }
                }
            }
            .saa-sidepanel-bottom-nav {
                a {
                    display: flex;
                    background: #F8F8F8;
                    padding: 5px;
                    border-radius: 5px;
                    svg {
                        color: #007185;
                    }
                    &.active {
                        background: #007185 !important;
                        svg {
                            color: white !important;
                        }
                    }
                }
            }
        }
        .saa-sidepanel-page {
            padding: 0px 0px 12px 58px;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            flex-direction: column;

            .skelet-line {
                display: block !important;
                padding: 0 !important;
                pointer-events: none;
                .mb-4 {
                    margin-bottom: 4px !important;
                }
                span {
                    background: linear-gradient(132deg, rgba(0, 0, 0, 0.0666666667) 33%, rgb(0 0 0 / 0%) 50%, rgba(0, 0, 0, 0.0666666667) 66%) #f2f2f2;
                    background-size: 300% 100%;
                    animation: l1 1s infinite linear;
                    border-radius: 5px;
                    height: -webkit-fill-available;
                    display: block;
                    margin: 0 !important;
                    @keyframes l1 {
                        0% {background-position: right}
                    }
                    &.h-i {
                        height: inherit !important; 
                    }
                }
                &.p-brand-skelet {
                    padding: 2px 2px 2px 0px;
                    height: 16px;
                    margin-bottom: 4px;
                }
            }

            .saa-sidepanel-error {
                margin: 20px 0px 0px 0px;
                padding: 10px;
                background: #F7D5D5;
                border-radius: 5px;
                max-width: 526px;
                z-index: 3;
                font-size: 12px;
                font-weight: 400;
                line-height: 15px;

                .attention {
                    color: #B12704;
                }
            }

            .saa-sidepanel-page-container {
                max-width: 526px;
                width: 100%;

                .saa-module-title {
                    font-size: 14px;
                    font-weight: 500;
                    line-height: 17.5px;
                }

                .saa-sidepanel-first-banner {
                    width: -webkit-fill-available;
                    background: #ECECEC;
                    padding: 12px;
                    margin-top: 20px;
                    border-radius: 5px;
                    border: 1px solid #D3D3D3;

                    .btn-banner-close {
                        position: absolute;
                        right: 10px;
                        top: 25px;
                        cursor: pointer;
                    }

                    .banner-title {
                        font-size: 16px;
                        font-weight: 500;
                        line-height: 22px;
                        letter-spacing: 0px;
                        margin-bottom: 6px;
                        display: block;
                    }

                    .banner-text {
                        font-size: 14px;
                        font-weight: 400;
                        line-height: 17.5px;
                        text-align: left;
                        margin-bottom: 0;
                    }
                }

                .saa-search-product-item {
                    margin-bottom: 20px;
                    padding: 5px;
                    border-radius: 5px;
                    background: #F8F8F8;

                    &:hover {
                        .saa-sidepanel-short-container {
                            .saa-sidepanel-product-image {
                                .image-btns {
                                    display: flex;
                                    justify-content: space-between;
                                }
                            }
                        }
                    }

                    .saa-sidepanel-calc-container {
                        margin-top: 18px;
                        display: flex;
                        justify-content: end;
                        flex-direction: column;
                        max-width: 526px;
        
                        .buy-box-container {
                            border-radius: 5px;
                            background-color: #FFF0A5;
                            display: flex;
                            justify-content: center;
                            margin-top: 8px;
                            font-size: 14px;
                            height: 26px;
                            align-items: center;
                        }

                        .red-icon-container > .saa-tooltip-container:first-child .gate-icon.skelet-line {
                            margin: 0px !important;
                        }

                        .red-icon-container {
                            display: flex;
                            .red-icon {
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                background: #F7D5D5;
                                width: 26px;
                                height: 26px;
                                padding: 4px;
                                border-radius: 5px;
                                margin-right: 6px;
                                color: rgb(177, 39, 4);
                            }
        
                            .grey-icon {
                                cursor: pointer;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                background: #FFFFFF;
                                width: 26px;
                                height: 26px;
                                margin-left: 6px;
                                border-radius: 5px;
                                padding: 4px;
                            }
        
                            .favorite {
                                background: #15B91D1A !important;
                                svg {
                                    color: #007600 !important;
                                }
                            }
        
                            .blacklisted {
                                background: #F7D5D5 !important;
                                svg {
                                    color: #B12704 !important;
                                }
                            }
        
                            .gate-icon {
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                background: #FFFFFF;
                                width: 26px;
                                height: 26px;
                                margin-left: 6px;
                                border-radius: 5px;
                                padding: 4px;
        
                                &:has(.saa-color-blue) {
                                    background: #0066C01A !important;
                                }
        
                                .saa-color-red {
                                    color: rgb(177, 39, 4);
                                }
        
                                .saa-color-green {
                                    color: #007600;
                                }
        
                                .saa-color-blue {
                                    color: #4599df;
                                }
                            }
                        }
        
                        .offers-container {
                            display: flex;
                            justify-content: space-between;

                            div:first-child:nth-last-child(1),
                            div:first-child:nth-last-child(1) ~ div {
                                width: 100% !important;
                            }
                            div:first-child:nth-last-child(3),
                            div:first-child:nth-last-child(3) ~ div {
                                width: 31% !important;
                            }

                            .fbm, .fba, .amazon {
                                margin-top: 8px;
                                width: 49%;
                                background-color: #C4E4FF;
                                border-radius: 5px;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                height: 26px;
                                font-size: 14px;
                                b {
                                    font-weight: 500;
                                    i {
                                        font-style: normal;
                                        margin-left: 2px;
                                    }
                                }
                                span {
                                    font-weight: 400;
                                    margin-right: 4px;
                                }
                            }
                            .fba {
                                background-color: #FFD1C2 !important;
                            }
                            .amazon {
                                text-align: center;
                                background-color: #FFA50080 !important;
                                span {
                                    margin-right: 0px !important;
