                        border: none;
                    }
                    .sales-container {
                        margin-top: 4px;
                        width: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        padding: 10px;
                        font-size: 13px;
                        .sales-text, .apply-cog-text {
                            text-wrap: nowrap;
                            text-overflow: ellipsis;
                            overflow: hidden;
                            display: block;
                        }
                        .apply-cog-container {
                            display: flex;
                            align-items: center;
                        }
                    }
                    &.open-slide {
                        animation: opn 0.25s ease-in-out;
                        transform: translateY(-5px);
                    }
                    &.close-slide {
                        animation: cls 0.25s ease-in-out;
                        transform: translateY(-110px);
                    }
                }
                @keyframes opn {
                    0% {transform: translateY(-110px);}
                    100% {transform: translateY(-5px);}
                }
                @keyframes cls {
                    0% {transform: translateY(-5px);}
                    100% {transform: translateY(-110px);}
                }
            }
            .inputs-container {
                display: flex;
                justify-content: space-between;
                z-index: 2;
                position: relative;
                background: #f8f8f8;
                .text-on-input {
                    display: flex;
                    align-items: center;
                    position: relative;
                    width: 49%;
                    margin-top: 5px;
                    .prefix {
                        margin-left: 14px;
                        position: absolute;
                    }
                    .after {
                        position: absolute;
                        right: 10px;
                        display: flex;
                    }
                    textarea {
                        overflow-y: hidden;
                        min-height: 64px !important;
                        field-sizing: content;
                        box-shadow: none;
                        padding: 10px;
                        border-radius: 6px;
                        border: 1px solid #ced4da;
                        width: 100%;
                    }
                    &.w-100 {
                        width: 100%;
                    }
                }
                .text-on-input label {
                    text-transform: uppercase;
                    line-height: normal;
                    position: absolute;
                    top: -9px;
                    left: 14px;
                    padding: 2px;
                    z-index: 1;
                    font-size: 12px;
                    &.transform-auto {
                        text-transform: math-auto;
                    }
                }
                .text-on-input label:after {
                    content: " ";
                    background-color: #fff;
                    width: 100%;
                    height: 13px;
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    z-index: -1;
                }
                label {
                    font-size: 16px;
                    font-weight: 500;
                    display: inline-block;
                    margin-bottom: .5rem;
                }
                .form-control {
                    box-shadow: none;
                    padding: .375rem .75rem .375rem 2rem;
                    height: 44px;
                    color: black;
                    border-radius: 6px;
                    &.padding-link-icon {
                        padding: .375rem 2.3rem .375rem .75rem;
                        text-overflow: ellipsis;
                        overflow: hidden;
                        white-space: nowrap;
                    }
                    &.error {
                        border-color: #ff3400;
                    }
                }
                .form-control[readonly] {
                    background-color: #fff;
                }
            }

            .saa-sidepanel-product-calc {
                margin-top: 18px;
                display: flex;
                justify-content: end;
                flex-direction: column;
                max-width: 526px;
                padding: 5px;
                border-radius: 5px;
                background: #f8f8f8;

                .red-icon-container > .saa-tooltip-container:first-child .gate-icon.skelet-line {
                    margin: 0px !important;
                }

                .red-icon-container {
                    display: flex;
                    .red-icon {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        background: #F7D5D5;
                        width: 26px;
                        height: 26px;
                        padding: 4px;
                        border-radius: 5px;
                        margin-right: 6px;
                        color: rgb(177, 39, 4);
                    }

                    .grey-icon {
                        cursor: pointer;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        background: #FFFFFF;
                        width: 26px;
                        height: 26px;
                        margin-left: 6px;
                        border-radius: 5px;
                        padding: 4px;
                    }

                    .favorite {
                        background: #15B91D1A !important;
                        svg {
                            color: #007600 !important;
                        }
                    }

                    .blacklisted {
                        background: #F7D5D5 !important;
                        svg {
                            color: #B12704 !important;
                        }
                    }

                    .gate-icon {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        background: #FFFFFF;
                        width: 26px;
                        height: 26px;
                        margin-left: 6px;
                        border-radius: 5px;
                        padding: 4px;

                        &:has(.saa-color-blue) {
                            background: #0066C01A !important;
                        }

                        .saa-color-red {
                            color: rgb(177, 39, 4);
                        }

                        .saa-color-green {
                            color: #007600;
                        }

                        .saa-color-blue {
                            color: #4599df;
                        }
                    }
                }

                .offers-container {
                    display: flex;
                    justify-content: space-between;
                    
                    div:first-child:nth-last-child(1),
                    div:first-child:nth-last-child(1) ~ div {
                        width: 100% !important;
                    }
                    div:first-child:nth-last-child(3),
                    div:first-child:nth-last-child(3) ~ div {
                        width: 31% !important;
                    }

                    .fbm, .fba, .amazon {
                        margin-top: 8px;
                        width: 49%;
                        background-color: #C4E4FF;
                        border-radius: 5px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        height: 26px;
                        font-size: 14px;
                        b {
                            font-weight: 500;
                        }
                        span {
                            font-weight: 400;
                            margin-right: 4px;
                        }
                    }
                    .fba {
                        background-color: #FFD1C2 !important;
                    }
                    .amazon {
                        text-align: center;
                        background-color: #FFA50080 !important;
                        span {
                            margin-right: 0px !important;
                        }
                    }
                }
                .bsr-container {
                    border-radius: 5px;
                    background-color: #ffffff;
                    display: flex;
                    justify-content: center;
                    margin-top: 8px;
                    font-size: 14px;
                    height: 26px;
                    align-items: center;

                    .saa-copy {
                        margin-right: 4px;
                        min-width: 16px;
                    }

                    .bsr-title {
                        margin-left: 6px;
                    }

                    .bsr-value {
                        color: #007600;
                        background-color: #15B91D1A;
                        border-radius: 5px;
                        margin: 0 4px 0 4px;
                        padding: 2px 4px 2px 4px;
                        height: 22px;
                        display: flex;
                        align-items: center;
                    }

                    .category {
                        margin-left: 4px;
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                    }
                }

                .sales-and-cog-container {
                    display: flex;
                    justify-content: space-between;
                    margin-top: 8px;

                    .sales {
                        width: 49%;
                        background-color: #ffffff;
                        border-radius: 5px;
                        height: 26px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        font-size: 14px;
                        padding: 0px 4px 0px 4px ;
                        b {
                            font-weight: 500;
                            white-space: nowrap;
                        }
                        span {
                            font-weight: 400;
                            margin-right: 4px;
                            text-overflow: ellipsis;
                            white-space: nowrap;
                            overflow: hidden;
                        }
                    }
                    .cog {
                        width: 49%;
                        background-color: #ffffff;
                        border-radius: 5px;
                        height: 26px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        font-size: 14px;
                        .max-cog {
                            margin-left: 6px;
                        }
                        b {
                            font-weight: 500;
                            white-space: nowrap;
                            margin-right: 6px;
                        }
                        span {
                            font-weight: 400;
                            margin-right: 4px;
                        }
                    }
                }

                .profit-container {
                    border-radius: 5px;
                    display: flex;
                    justify-content: space-between;
                    margin-top: 20px;
                    background-color: #ffffff;
                    .profit, .roi {
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        width: 49%;
                        font-size: 14px;
                        font-weight: 700;
                        line-height: 18px;
                        .title {
                            padding: 5px;
                        }
                        .value {
                            width: 90%;
                            text-align: center;
                            border-radius: 5px;
                            margin-bottom: 6px;
                        }
                    }
                }
                .note {
                    font-size: 12px;
                    font-weight: 400;                    
                }
            }
        }
    }
}